#your-product-screen {
    height: auto;
    width: 100%;
    /* border: 1px solid black; */
  }
  
  #your-product-screen > #products {
    height: auto;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    /* border: 1px solid black; */
  }
  
  #your-product-screen > #products > .product {
    width: 18%;
    height: auto;
    border-radius: 5px;
    border: 1px solid rgb(190, 190, 190);
  }
  
  #your-product-screen > #products > .product > .image {
    height: 200px;
    width: 100%;
    /* border: 1px solid black; */
  }
  
  #your-product-screen > #products > .product > .image > img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
  
  #your-product-screen > #products > .product > .details {
    width: 100%;
    padding: 0 20px 10px 20px;
    /* border: 1px solid black; */
  }